/* ==============================================
    Sidebar Actions
     A. Sidebar Left Toggle
     B. Sidebar Right Toggle
     C. Left Sidebar Minified Helper
     D. Mobile Menu Modifications
=================================================
  A. Sidebar Left Toggle
================================================= */
/*left sidebar open*/
.sb-l-o #sidebar_left {
  left: 0;
  overflow: hidden;
}
.sb-l-o #content_wrapper {
  margin-left: 230px;
}
/*left sidebar closed*/
.sb-l-c #sidebar_left {
  left: -230px;
