  color: #888;
}
#content-footer .footer-meta {
  padding-right: 30px;
}
#content-footer .footer-return-top {
  text-align: center;
  position: absolute;
  top: -3px;
  right: 5px;
  width: 25px;
  height: 25px;
  background-color: #eaeaea;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
#content-footer .footer-return-top span {
  position: relative;
  top: 3px;
  color: #AAA;
  font-size: 12px;
  -webkit-transition: all 0.23s ease;
  transition: all 0.2s ease;
}
#content-footer .footer-return-top:hover {
  cursor: pointer;
  background-color: #e2e2e2;
}
#content-footer .footer-return-top:hover span {
  color: #666;
}
/* Fixed Setting */
#content-footer.affix {
  z-index: 1;
  position: fixed;
  margin-left: 230px;
}
/* Match Sidebar States */
body.sb-l-c #content-footer.affix {
  margin-left: 0;
}
body.sb-l-m #content-footer.affix {
  margin-left: 60px;
}
@media (max-width: 900px) {
  body.sb-l-m #content-footer.affix {
    margin-left: 45px;
  }
}
