================================================= */
body.timeline-page #main {
  min-height: 1400px;
}
body.timeline-page #content {
  padding: 8px 35px 50px 35px;
}
#timeline {
  position: relative;
}
#timeline:after {
  position: absolute;
  top: 0;
  left: 50%;
  width: 4px;
  margin-left: -2px;
  height: 100%;
  content: "";
  background: #ddd;
}
#timeline .timeline-divider {
  position: relative;
  margin: 10px 0 45px;
  z-index: 3;
}
#timeline .timeline-divider .divider-label {
  position: relative;
  width: 110px;
  margin: 0 auto;
  padding: 5px;
  text-align: center;
  border: 1px solid #DDD;
  color: #777;
  font-weight: 600;
  background: #FFF;
}
#timeline .timeline-divider .divider-label:before {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  left: 0;
  top: 100%;
  background: #4a89dc;
}
#timeline .panel {
  position: relative;
  z-index: 11;
  overflow: visible;
  margin-bottom: 30px;
}
#timeline .panel:before,
#timeline .panel:after {
  content: "";
  z-index: 2;
  position: absolute;
  top: 10px;
  left: 99.8%;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 8px 0 8px 12px;
  border-color: transparent transparent transparent #fafafa;
}
#timeline .panel:after {
  z-index: 1;
  border-left-color: #666;
}
#timeline .right-column .panel:before,
#timeline .right-column .panel:after {
  top: 10px;
  right: 99.8%;
  left: auto;
  border-width: 8px 12px 8px 0;
  border-color: transparent #fafafa transparent transparent;
}
#timeline .right-column .panel:after {
  border-right-color: #666;
}
#timeline > .row > .left-column .timeline-item {
  padding-right: 30px;
}
#timeline > .row > .right-column .timeline-item {
  padding-left: 30px;
}
#timeline > .row > .right-column {
  margin-top: 80px;
}
@media (max-width: 770px) {
  #timeline > .row > .left-column .panel {
    margin-right: 0;
  }
  #timeline > .row > .right-column .panel {
    margin-left: 0;
  }
  #timeline > .row .panel:before {
    display: none;
  }
  #timeline > .row .panel:after {
    display: none;
  }
}
#timeline .tab-content {
  min-height: 0;
}
