  width: 38px;
  height: 38px;
  line-height: 38px;
  font-size: 20px;
  color: #FFF;
  text-align: center;
  border-radius: 50%;
  background: #eeeeee;
  box-shadow: 0 0 0 5px #eeeeee;
}
